@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.page-contact {
  .l-content {
    .l-main {
      padding-bottom: 50px;
    }
  }
  h3.lead {
    font-size: 24px;
    margin-bottom: 1em;
    background: #eee;
    text-indent: 1em;
    line-height: 60px;
  }
  h2.bukkenlist {
    font-size: 24px;
    margin-bottom: 2em;
    margin-left:0.5em;
    text-indent: 0.5em;
    line-height: 30px;
    border-left: 10px solid $color-primary;
  }
	h2.lead{
	  border-left: 10px solid $color-primary;
		font-size: 22px;
		line-height: 29px;
    margin: 10px 0 0;
		padding-left: 13px;
	}
  .titles {
    width: 740px;
    border-bottom-width: 2px;
    border-bottom-style: solid;
    border-bottom-color: #999;
    margin-right: auto;
    margin-left: auto;
    font-size: 14px;
    font-weight: bold;
    padding-top: 5px;
    padding-right: 5px;
    padding-bottom: 2px;
    padding-left: 5px;
  }
  .textL {
    width: 740px;
    margin-right: auto;
    margin-left: auto;
  }
  .textR {
    text-align: right;
    width: 740px;
    margin: 10px auto 0;
    padding-bottom: 10px;
  }
  .titlem {
    border-left: 7px solid $color-primary;
    width: 720px;
    margin: 10px auto 0;
    font-size: 14px;
    padding: 5px;
    font-weight: bold;
  }
  .att {
    padding: 30px;
    width: 100%;
    background: #f1f1f1;
    strong {
      color: #F00;
    }
  }
  .kozin {
    border: 1px solid #ccc;
    width: 740px;
    margin: auto;
    height: 200px;
    overflow: scroll;
  }
  .doui {
    background: #f7f7f7;
    width: 740px;
    text-align: center;
    margin: 20px auto;
    padding: 20px 0;
  }
  .touroku {
    width: 740px;
    text-align: center;
    margin: 20px auto;
    padding: 20px 0;
  }
  a:hover img {
    opacity: 0.8;
    filter: alpha(opacity = 80);
    -ms-filter: "alpha( opacity=80 )";
    background: #fff;
  }
  div.lead {
    width: 820px;
    font-size: 18px;
    margin: 0 auto 1em;
    background: #eee;
    text-indent: 1em;
    line-height: 48px;
  }

  .toi_thanks {
    width: 600px;
    margin: 80px auto 40px auto;
    font-size: 14px;
    overflow: hidden;
    p {
      margin-bottom: 20px;
    }
  }

  .submitArea {
    .button {
      width: 440px;
      margin: 0 auto;
      letter-spacing: 10px;
    }
  }

  /* ----- 会員登録のメリット -----*/

  #main_member {
    width: 940px;
    margin-left: 10px;
    font-size: 1em;
    float: left;
  }

  #title_member, #title_member_n {
    width: 940px;
    height: 447px;
    background: url(img/login3/img_main.jpg) no-repeat;
    position: relative;
    h2 {
      position: absolute;
      left: 50px;
      top: 222px;
      width: 398px;
      height: 122px;
      z-index: 2;
      padding: 0;
      margin: 0;
    }
    .btn {
      position: absolute;
      right: 46px;
      top: 288px;
      width: 296px;
      height: 50px;
      z-index: 2;
    }
    .txt {
      position: absolute;
      left: 24px;
      top: 336px;
      width: 896px;
      height: 43px;
      z-index: 2;
    }
    a:hover img {
      opacity: 0.7;
      filter: alpha(opacity = 70);
      -ms-filter: "alpha( opacity=70 )";
      background: #fff;
    }
  }

  #merit_member {
    width: 940px;
  }

  #merit_more {
    width: 899px;
    background: #79c40a;
    margin: 0px 20px;
    h3 {
      margin-bottom: 20px;
    }
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      width: 879px;
      margin-left: 20px;
    }
    li {
      display: block;
      width: 420px;
      height: 185px;
      margin: 0;
      padding: 0;
      float: left;
      margin-right: 19px;
      margin-bottom: 20px;
    }
    .step {
      margin: 10px 0 0 0;
    }
  }

  #member_touroku {
    width: 940px;
    margin: auto;
  }

  .merit_con {
    margin: 0 0 20px 20px;
    display: inline;
  }

  .merit_con01 {
    width: 94px;
    margin-left: 5px;
    float: left;
  }

  .merit_con02 {
    width: 312px;
    height: 140px;
    margin: 5px 0 0 0;
    line-height: 1.3em;
    float: left;
    font-size: 0.9em;
  }

  .merit_con_title {
    margin-bottom: 13px;
  }

  .bred {
    color: #cc0000;
    font-weight: bold;
  }

  .att {
    .att_l {
      width: 600px;
      float: left;
    }
    .att_r {
      float: right;
      width: 265px;
    }
  }

  .estatetablecenter {
    border-right-width: 1px;
    border-left-width: 1px;
    border-right-style: dotted;
    border-left-style: dotted;
    border-right-color: #CCCCCC;
    border-left-color: #CCCCCC;
  }

  .table-es td {
    padding: 5px;
  }

  .table-es001 {
    border-bottom: 1px dotted #ccc;
  }

  .table-es002 {
    border-bottom: 1px dotted #ccc;
    border-right: 1px dotted #ccc;
  }

  .estatetable {
    background-color: #FFF;
    border: 1px solid #999;
    padding: 5px;
    margin: 10px 3px;
  }
  .pointer{
    cursor: pointer;
  }

  .chusyaku {
    color: #ff0000;
    font-size: 0.7em;
  }

  .r-tokuten {
    width: 950px;
    height: 376px;
    position: relative;
    margin: auto;
    ul {
      padding: 180px 0 0 50px;
      list-style: none;
    }
    li {
      float: left;
      padding-right: 10px;
    }
  }

  dl.form_regist {
    border-bottom: 1px solid #ccc;
    background: #eee;
    dt, dd {
      border-top: 1px solid #ccc;
    }
    dt.addr, dd.addr {
      border-top: 1px dashed #ccc;
    }
    dt {
      padding: 30px;
      width: 300px;
      float: left;
      position: relative;
      &.one {
        width: 100%;
        float: none;
      }
      &.col_left {
        width: 250px;
      }
    }
    dd {
      background: #fff;
      margin-left: 300px;
      border-left: 1px dashed #ccc;
      padding: 25px 15px;
      position: relative;
      &.one {
        border-top: 1px dashed #ccc;
        margin-left: 0;
        border-left: 0;
        padding: 26px 32px;
        label {
          width:140px;
        }
      }
      &.col_day {
        float: left;
        margin-left: 0;
        width: 380px;
        height: 90px;
      }
      &.col_time {
        margin-left: 680px;
        width: 260px;
        height: 90px;
      }
      &:after {
        content: '';
        display: block;
        clear: both;
        &.col_day {
          content: '';
          display: inline-block;
          clear: none;
        }
      }
      &.col_right {
        margin-left: 250px;
      }
    }
    dt span.hissu_icon {
      position: absolute;
      right: 32px;
      top: 32px;
      width: 42px;
      text-align: center;
      line-height: 20px;
      font-size: 13px;
      &.hissu {
        background: #e60012;
        color: #fff;
      }
      &.nini {
        background: #aaa;
        color: #fff;
      }
    }
    input {
      &[type="text"], &[type="tel"], &[type="date"], &[type="email"] {
        width: 180px;
        padding: 10px;
        margin: 0 0.5em;
        box-sizing: border-box;
      }
    }
    select {
      width: 210px;
      margin: 0 0.5em;
      box-sizing: border-box;
    }
    select {
      &.month, &.day {
        width: 64px;
      }
      &.time {
        width: 128px;
      }
    }
  }

  .coution_bg {
    background: #ffdcdc;
  }

  dl {
    &.form_regist {
      input {
        &[name="add"] {
          width: 320px;
        }
        &[name="add2"], &#addr, &[name="questionnaire_keyword"], &[name="bukken_address"], &[name="addr"] {
          width: 520px;
        }
        &[name="address"] {
          width: 500px;
        }
        &[name="tel"], &[name="tel2"], &[name="fax"], &[name="mob"] {
          width: 300px;
          margin-bottom: 1em;
        }
        &[name="mail"], &[name="email"], &[name="kmail"] {
          width: 520px;
          margin-bottom: 1em;
          padding: 10px;
          box-sizing: border-box;
        }
      }
      select {
        &[name="1time_from"], &[name="1time_to"], &[name="2time_from"], &[name="2time_to"], &[name="3time_from"], &[name="3time_to"] {
          width: 60px;
        }
      }
      textarea {
        width: 520px;
        margin-bottom: 1em;
        resize: vertical;
        margin-left:0.5em;
        margin-right: 0.5em;
				&#houmon_date{
				  width: 400px;
				}
      }
      label {
        display: inline-block;
        padding: 4px 1em 1em 0;
      }
    }
    &.bukken_area {
      overflow: hidden;
      margin-bottom: 30px;
      border-top: 3px solid $color-primary;
      .bukken_title {
        @include font-line(20, 24);
        font-weight: 700;
        letter-spacing: 0.2em;
        margin: 0;
        overflow: hidden;
        padding: 10px;
        border-bottom: 1px;
        color: $color-text01;
        .del {
          float: left;
          margin-right: 4em;
        }
        .shubetu {
          display: inline-block;
          vertical-align: text-top;
          letter-spacing: 0;
          .new {
            background-color: $color-sub;
            border: none;
            width: 90px;
            color: #fff;
          }
          .pricedown {
            background-color: $color-main;
            border: none;
            width: 90px;
            color: #fff;
          }
          @include clearfix();
        }
        .addr {
          float: left;
        }
      }
      .bukken_info {
        padding: 20px 0;
        margin: 0 10px;
        box-sizing: border-box;
        position: relative;
        font-size: 15px;
        line-height: 1.75;
        background: linear-gradient(to right, $color-text01 62.5%, rgba(255, 255, 255, 0) 0%) repeat-x;
        background-size: 8px 1px;
        p {
          margin-bottom: 1em;
        }
        .pic {
          width: 285px;
          height: 250px;
          background: #fff;
          float: left;
          margin-bottom: 20px;
          margin-right: 20px;
          position: relative;
          img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            font-family: 'object-fit: contain;';
          }
          .stamp {
            width: 116px;
            height: 116px;
            border-radius: 50%;
            background-color: #000;
            color: #fff;
            @include font-line(16, 18);
            @include opacity(0.6);
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            z-index: 2;
          }
        }
        .state {
          flex: 1 1 auto;
          margin-left: 10px;
          dl {
            color: $color-text01;
            display: flex;
            align-items: center;
            margin: 0 auto;
            min-height: 40px;
            border-bottom: 1px dashed $color-text01;
            dt {
              @include font-line(16, 17);
              flex: 0 0 95px;
              padding: 0 20px;
            }
            dd {
              flex: auto;
              margin: 0;
              padding-right: 20px;
            }
          }
        }
        .addr {
          color: #0061c4;
          font-size: 20px;
          margin-bottom: 1em;
        }
        .price {
          color: #f71d4b;
          font-size: 18px;
          margin-bottom: 1em;
        }
        table {
          width: 400px;
          position: absolute;
          right: 20px;
          top: 10px;
          background-color: #ececec;
          th {
            text-align: center;
            background-color: #ececec;
            padding: 4px;
          }
          td {
            text-align: center;
            background-color: #fff;
            padding: 4px;
          }
        }
      }
    }
  }

  #yui-gen0 table {
    width: auto;
    th, td {
      padding: 0;
    }
  }
}
